<template>
  <div class="container" @click="clickContainer">
    <hls-video
      class="video-stream"
      :class="{ blurred: menuState == 1 }"
      :video-src="videoSrc"
    />
    <div class="menu-overlay">     
      <transition name="menu-transition">
        <div v-if="menuState == 1" class="sources-menu">
          <div class="menu-header">Camera Source</div>
          <div
            @click="selectSource(i, $event)"
            class="source-button"
            v-for="(item, i) in sources"
            :key="i"
          >
            {{ toTitleCase(item.alias) }}
          </div>
        </div>
      </transition>
    </div>
    <div class="cam-label-wrapper">
      <transition name="cam-label-transition">
        <div @click="showSourcesMenu" v-if="menuState == 0" class="cam-label">{{ camLabel }}</div>
      </transition>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      sources: null,
      currentSourceIndex: 0,
      videoSrc: "",      
      menuState: 0, //0:none, 1:sources shown      
    };
  },
  watch: {
    camSources(val) {
      this.sources = val;
      this.currentSourceIndex = this.initIndex;
      this.applySourceIndex();            
    },
  },
  props: ["streamUri","camSources", "fullScreen", "initIndex"],
  methods: {
    toTitleCase(str) {
      return str.replace(/\w\S*/g, function (txt) {
        return txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase();
      });
    },

    applySourceIndex() {
      this.videoSrc =
        this.streamUri + "/" + this.sources[this.currentSourceIndex].streamUri;
    },
    selectSource(index, e) {
      this.currentSourceIndex = index;
      this.applySourceIndex();
      this.menuState = 0;
      e.stopImmediatePropagation();
    },
    showSourcesMenu(e) {      
      this.menuState = 1;
      e.stopImmediatePropagation();
    },
    clickContainer(e) {
      if (this.fullScreen) {
        if (this.menuState == 0) {
          this.emitExit(e);
        }else{
          this.menuState = 0;
        }
      } else {
        this.emitEnter(e);
      }
    },
    emitEnter(e) {     
      this.$emit("enter");
      
      //   }
    },
    emitExit(e) {
      //revert to front door
      // this.currentSourceIndex = 2;
      // this.applySourceIndex();

      this.$emit("exit");
      
      e.stopImmediatePropagation();
    },
  },
  computed: {
    camLabel() {
      return this.toTitleCase(
        this.sources ? this.sources[this.currentSourceIndex].alias : ""
      );
    },
  },
  mounted() { 
    console.log(this.streamUri)
  },
};
</script>

